export const getBranch = (branchCode, course) => {
  if (!branchCode || !course) return;
  let BRANCH;
  switch (course) {
    case "BE":
      BRANCH = BRANCH_BE;
      break;
    case "PDDC":
      BRANCH = BRANCH_PDDC;
      break;
    case "PG":
      BRANCH = BRANCH_PG;
      break;
    default:
      return;
  }
  for (var prop in BRANCH) {
    if (BRANCH[prop] == branchCode) return prop;
  }
};
