import React, { useState } from "react";
import Axios from "axios";
import SearchDocForm from "./SearchDocForm";
import DocumentModel from "./DocumentModel";
import CertificateModel from "./CertificateModel";
import DocsApprove from "./DocsApprove";
import { connect } from "react-redux";
import Certificate from "../Certificate/Certificate";
import SearchDocs from "./SearchDocs";

function FindDocument(props) {
  const [documents, setDocuments] = useState();
  const [selecteDocument, setSelecteDocument] = useState([]);
  const [certificate, setCertificate] = useState();
  const [loading, setLoading] = useState(false);

  const print = () => {
    const content = document.getElementById("printArea");
    const pri = document.getElementById("ifmContentToPrint").contentWindow;
    pri.document.open();
    pri.document.write(content.innerHTML);
    pri.document.close();
    pri.focus();
    setTimeout(() => {
      pri.print();
    }, 500);
  };

  const handleSubmit = async (values) => {
    console.log(values);

    const formData = new FormData();
    Object.keys(values).forEach((field) => {
      if (field === "date" && values[field]) {
        formData.append(field, values[field].replaceAll("-", "/"));
      } else formData.append(field, values[field]);
    });

    await Axios.post("/api/admin/findDocument", formData)
      .then((res) => {
        console.log(res.data);
        setDocuments([]);
        const data = res.data;
        let doc = [];
        data.map((d) => {
          d.request.map((req) => {
            doc.push({ ...d, request: undefined, ...req });
          });
        });
        setDocuments(doc);
      })
      .catch((err) => console.log(err));
  };

  const renderDocTable = () => {
    if (!documents) return;
    if (documents.length === 0) {
      return <h1 className="text-center">No Document Found!</h1>;
    }

    return (
      <>
        <SearchDocs
          documents={documents}
          selecteDocument={selecteDocument}
          setSelecteDocument={setSelecteDocument}
          setCertificate={setCertificate}
        />
        <CertificateModel data={certificate} />
        {renderPrintBtn()}
      </>
    );
  };

  const renderPrintBtn = () => {
    if (props.role !== "ROLE_SSHEAD") return;

    return (
      <div className="text-center mb-4">
        <button
          className="btn btn-success btn-lg"
          onClick={print}
          disabled={selecteDocument.length === 0}
          title={
            selecteDocument.length === 0
              ? "First Select atleast one Certificate!"
              : "Print"
          }
        >
          Print All Selected Certificate
        </button>
        <div style={{ display: "none" }}>
          <div id="printArea">
            {selecteDocument &&
              selecteDocument.map((i) => (
                <Certificate
                  key={documents[i].request_id}
                  data={documents[i]}
                />
              ))}
          </div>
        </div>
        <iframe
          id="ifmContentToPrint"
          title="printer_frame"
          style={{
            height: "0px",
            width: "0px",
            position: "absolute",
          }}
        />
      </div>
    );
  };

  return (
    <div className="container mt-5">
      <SearchDocForm onSubmit={handleSubmit} />
      {renderDocTable()}
    </div>
  );
}

const mapStateToProps = (state) => {
  return { role: state.User.credentials.role };
};

export default connect(mapStateToProps)(FindDocument);
