import React from "react";
import { contentStyle } from "./CertiStyle";

const Conduct = ({ data }) => {
  const {
    gender,
    name,
    course,
    branch,
    enrollment,
    addmission_year,
    graduation_year,
    cgpa,
  } = data;
  return (
    <div style={contentStyle}>
      <p style={{ textIndent: "36pt", marginBottom: "20pt" }}>
        This is to certify that{" "}
        <u style={{ textTransform: "uppercase" }}>{gender}.</u>{" "}
        <u style={{ textTransform: "uppercase" }}>{name}</u> was student of this
        College from JUNE -{" "}
        <u style={{ textTransform: "uppercase" }}>{addmission_year}</u> to MAY -{" "}
        <u style={{ textTransform: "uppercase" }}>{graduation_year}</u> in{" "}
        <u style={{ textTransform: "uppercase" }}>{course}</u>{" "}
        <u style={{ textTransform: "uppercase" }}>{branch}</u> [Enrolment No.{" "}
        <u style={{ textTransform: "uppercase" }}>{enrollment}</u>].
      </p>
      <p style={{ textIndent: "36pt", marginBottom: "20pt" }}>
        He has completed his study in MAY –{" "}
        <u style={{ textTransform: "uppercase" }}>{graduation_year}</u> in
        Regular course with CGPA{" "}
        <u style={{ textTransform: "uppercase" }}>{cgpa}</u>.
      </p>
      <p style={{ marginBottom: "20pt" }}>
        The course is approved by ALL INDIA COUNCIL FOR TECHNICAL EDUCATION NEW
        DELHI and college is affiliated to the Gujarat Technological University.
        The Medium of instruction and examination is in English.
      </p>
      <p style={{ textIndent: "36pt", marginBottom: "20pt" }}>
        He bears good moral character according to best of my knowledge.
      </p>
    </div>
  );
};

export default Conduct;
